import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import AOS from "aos";
import "aos/dist/aos.css";
import { useLanguage } from "../tools/LanguageContext";

function Home() {
  const { translations, toggleLanguage, language } = useLanguage();

  const cardData = [
    { title: translations.freeBets, value: translations.freeBetsDesc, route: "free_bets" },
    { title: translations.vip, value: translations.vipDesc, route: "vip" },
    { title: translations.statistics, value: translations.statisticsDesc, route: "statistics" },
    { title: translations.algorithm, value: translations.algorithmDesc, route: "algorithm" },
    { title: translations.poker, value: translations.pokerDesc, route: "poker" },
  ];

  useEffect(() => {
    AOS.init();
  }, []);

  return (
    <div className="bg-gray-900 text-white min-h-screen flex flex-col">
      {/* Header */}
      <header className="flex justify-between items-center p-4 bg-gray-800">
        <h1 className="text-3xl font-bold">{translations.title}</h1>
        <button
          onClick={toggleLanguage}
          className="bg-green-800 px-4 py-2 rounded text-lg hover:bg-green-900"
        >
          {language === "en" ? "Ελληνικά" : "English"}
        </button>
      </header>

      {/* Main Section */}
      <main className="p-4 flex-grow">
        <section className="flex flex-col md:flex-row items-center md:space-x-6 space-y-6 md:space-y-0">
          <div className="flex-1 text-center md:text-left">
            <h2 className="text-4xl font-bold mb-4">{translations.slogan}</h2>
            <Link to="about">
              <button className="bg-green-800 px-6 py-3 rounded text-lg hover:bg-green-900 mt-4">
                {translations.aboutUs}
              </button>
            </Link>
          </div>
          <div className="flex-1">
            <img
              src="images/art_of_betting.png"
              alt="Stadium"
              className="rounded-lg shadow-lg mx-auto"
            />
          </div>
        </section>

        {/* Cards Section */}
        <section className="mt-12 grid grid-cols-1 md:grid-cols-5 gap-6">
          {cardData.map((card, index) => (
            <div
              key={index}
              className="p-4 rounded-lg shadow-lg bg-green-800 text-center"
              data-aos={index % 2 === 0 ? "fade-left" : "fade-right"}
              data-aos-offset="150"
              data-aos-duration="1000"
            >
              <h3 className="text-2xl font-bold mb-2">{card.title}</h3>
              <p className="text-lg mb-4">{card.value}</p>
              <Link to={card.route}>
                <button className="bg-gray-800 text-white px-4 py-2 rounded hover:bg-gray-700">
                  {translations.learnMore}
                </button>
              </Link>
            </div>
          ))}
        </section>
      </main>
    </div>
  );
}

export default Home;
